
import './App.css';
import React, { Component } from 'react'
import Navbar from './Components/Navbar';
import NewsView from './Components/NewsView';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

export default class App extends Component {
  pageSize = 15; 
  render() {
    return (
      <div>
        <Router>
        <Navbar/>
        <Switch>
          <Route path="/science">
          <NewsView key="science" pageSize={this.pageSize}  country="in" category="science"/>
          </Route>
          <Route path="/business">
          <NewsView key="business" pageSize={this.pageSize}  country="in" category="business"/>
          </Route>
          <Route exact path="/entertainment">
          <NewsView key="entertainment" pageSize={this.pageSize}  country="in" category="entertainment"/>
          </Route>
          <Route exact path="/health">
          <NewsView key="health" pageSize={this.pageSize}  country="in" category="health"/>
          </Route>
          <Route exact path="/sports">
          <NewsView key="sports" pageSize={this.pageSize}  country="in" category="sports"/>
          </Route>
          <Route exact path="/technology">
          <NewsView key="technology" pageSize={this.pageSize}  country="in" category="technology"/>
          </Route>
          <Route exact path="/">
          <NewsView key="general" pageSize={this.pageSize}  country="in" category=""/>
          </Route>
        </Switch>
         </Router>
        </div>
    )
  }
}

