import React, { Component } from 'react'
import News from './News'
import Spinner from './Spinner';

export default class NewsView extends Component {
    constructor() {
        super();
        console.log("hello this is component");

        this.state = {
            articles: [],
            loading: false,
            page: 1

        }
    }
    

    async componentDidMount() {
        let url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=444567ac71554b94bd40735a1f19d094&page=1&pageSize=${this.props.pageSize}`;
        this.setState({loading: true});
        let data = await fetch(url);
        let parsedata = await data.json();    
        this.setState({ articles: parsedata.articles, 
            totalResults: parsedata.totalResults,
            loading:false})
            console.log(parsedata);
    }

    handleNexClick = async () => {
        if (this.state.page + 1 > Math.ceil(this.state.totalResults / this.props.pageSize)) {

        } else {
            let url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=444567ac71554b94bd40735a1f19d094&page=${this.state.page + 1}&pageSize=${this.props.pageSize}`;
            this.setState({loading:true});
            let data = await fetch(url);
            let parsedata = await data.json();
            console.log(parsedata);
            this.setState({
                page: this.state.page + 1,
                articles: parsedata.articles,
                loading: false
            })
        }

    }
    handlePreClick = async () => {
        let url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=444567ac71554b94bd40735a1f19d094&page=${this.state.page - 1}&pageSize=${this.props.pageSize}`;
        this.setState({loading: true});
        let data = await fetch(url);
        let parsedata = await data.json();
        this.setState({
            page: this.state.page - 1,
            articles: parsedata.articles,
            loading: false
        })
    }
    render() {

        return (

            <div className="container my-5">
                <h1 className="text-center">Latest News for {this.props.category}</h1>
                {this.state.loading && <Spinner />}
                <div className="row my-3">
                    {!this.state.loading && this.state.articles.map((element) => {
                        return <div className="col-md-4" key={element.url}>
                            <News title={element.title ? element.title.slice(0, 30) : ""} description={element.description ? element.description.slice(0, 80) : ""} imageUrl={element.urlToImage} url={element.url} author={element.author} publishAt={element.publishedAt}/>
                        </div>
                    })}

                </div>
                <div className="container d-flex justify-content-between">
                    <button type="button" disabled={this.state.page <= 1} onClick={this.handlePreClick} className="btn btn-dark">&larr; Previous</button>
                    <button type="button" disabled={this.state.page + 1 > Math.ceil(this.state.totalResults / this.props.pageSize)} onClick={this.handleNexClick} className="btn btn-dark">Next &rarr;</button>
                </div>
            </div>
        )
    }
}
