import React, { Component } from 'react'

export default class News extends Component {

    render() {
        let { title, description, imageUrl, url, author,publishAt } = this.props;
        return (
            <div >
                <div className="card h-100 my-3">
                    <img src={!imageUrl ? "https://media.istockphoto.com/photos/breaking-news-world-news-with-map-backgorund-picture-id1182477852?k=20&m=1182477852&s=612x612&w=0&h=I3wdSzT_5h1y9dHq_YpZ9AqdIKg8epthr8Guva8FkPA=" : imageUrl} className="card-img-top" alt="..." />
                    <div className="card-body">
                        <h5 className="card-title">{title}...</h5>
                        <p className="card-text text-danger">{description}...</p>
                        <a href={url} target="_blank" rel="noreferrer" className="btn btn-sm btn-dark">Read More</a>
                    </div>
                    <div className="card-footer">
                            <small className="text-muted">By {!author?"Unkown":author} on {new Date(publishAt).toGMTString()}</small>
                        </div>
                </div>
            </div>
        )
    }
}
